@font-face {
  font-family: "Cormorant Garamond";
  src:
    local("Cormorant Garamond Bold"),
    local("CormorantGaramond-Bold"),
    url("../fonts/CormorantGaramond-Bold.woff2") format("woff2"),
    url("../fonts/CormorantGaramond-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src:
    local("Cormorant Garamond Italic"),
    local("CormorantGaramond-Italic"),
    url("../fonts/CormorantGaramond-Italic.woff2") format("woff2"),
    url("../fonts/CormorantGaramond-Italic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src:
    local("Cormorant Garamond Medium Italic"),
    local("CormorantGaramond-MediumItalic"),
    url("../fonts/CormorantGaramond-MediumItalic.woff2") format("woff2"),
    url("../fonts/CormorantGaramond-MediumItalic.woff") format("woff");
  font-weight: 500;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src:
    local("Cormorant Garamond SemiBold"),
