.blog-item {
  display: flex;
  width: 100%;
  margin-bottom: 27px;

  text-align: center;

  @media (max-width: $tablet-max-width) {
    justify-content: center;
    margin-bottom: 47px;
  }

  @media (max-width: $mobile-max-width) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 40px;
  }
}

.blog-item__text-wrapper {
  flex-shrink: 0;
  width: 285px;
  max-height: 300px;
  padding: 67px 60px;

  background-color: $blog-background;

  @media (max-width: $tablet-max-width) {
    width: 339px;
    padding: 67px 85px;
  }

  @media (max-width: $mobile-max-width) {
    width: 290px;
    height: 256px;
    margin-top: -3px;
    padding: 45px 60px;
  }
}

.blog-item__text-wrapper p {
  margin: 0 0 13px 0;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  color: $basic-grey;
}

.blog-item__text-wrapper h3 {
  margin: 0 0 37px 0;
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-size: 26px;
  line-height: 34px;
  font-weight: 600;
  color: $basic-dark;
}

.blog-item__text-wrapper a {
  margin: 0 0 10px 0;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  color: $basic-dark;
  border-bottom: 1px solid $basic-brown;
  padding-bottom: 3px;
}


.blog-item img {
  width: 285px;
  height: 300px;

  object-fit: cover;

  @media (max-width: $tablet-max-width) {
    width: 339px;
  }

  @media (max-width: $mobile-max-width) {
    order: -1;
    width: 290px;
    height: 256px;
  }
}

.blog-item:nth-child(2n + 1) .blog-item__img {
  order: -1;
}

.blog-item:nth-child(n) .blog-item__img {
  @media (max-width: $mobile-max-width) {
    order: -1;
  }
}


.blog-item--large {
  position: relative;

  min-height: 630px;

  @media (max-width: $tablet-max-width) {
    position: static;
    min-height: 300px;
  }

  @media (max-width: $mobile-max-width) {
    //
  }
}

.blog-item--large img {
  width: 570px;
  height: 630px;


  @media (max-width: $tablet-max-width) {
    width: 339px;
    height: 300px;
  }

  @media (max-width: $mobile-max-width) {
    width: 290px;
    height: 256px;
  }
}

.blog-item--large .blog-item__text-wrapper {
  position: absolute;
  right: 0;
  bottom: 3px;
  width: 285px;
  height: 300px;

  @media (max-width: $tablet-max-width) {
    position: static;
    width: 339px;
    max-height: 300px;
    padding-top: 84px;
  }

  @media (max-width: $mobile-max-width) {
    width: 290px;
    height: 256px;
    padding-top: 62px;
  }
}

.blog-item a[href]:hover,
.blog-item a[href]:focus, {
  color: $basic-brown;
}


.blog-item a[href]:active, {
  opacity: 0.5;
}
