.faq__wrapper {
  max-width: 1060px;
  padding: 45px;
  margin: 0 auto;
  padding-top: 29px;

  @media (max-width: $tablet-max-width) {
    padding-top: 7px;
  }

  @media (max-width: $mobile-max-width) {
    padding: 10px 15px 14px 15px;
  }
}

.faq__wrapper h2 {
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 130%;
  letter-spacing: 0.03em;
  text-align: center;
  margin: 0 0 48px 0;

  @media (max-width: $mobile-max-width) {
    margin-bottom: 28px;
  }
}

.faq__list {
  border: 1px solid $basic-brown;
  list-style: none;
  padding: 0;
  margin: 0;
}

.faq__item {
  position: relative;
  padding: 43px 43px 43px 43px;
  cursor: pointer;

  @media (max-width: $mobile-max-width) {
    padding: 16px 18px 20px 18px;
  }

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: $basic-brown;
    bottom: 0;
    left: 0;

    @media (max-width: $mobile-max-width) {
      bottom: 3px;
    }
  }

  &:last-child::after {
    display: none;
  }

  &:hover,
  &:focus {
    background-color: rgba($basic-brown, 0.3);
    transition: 0.3s;
  }

  &:active {
    background-color: rgba($basic-brown, 0.7);
    transition: 0.3s;
  }
}

.faq__item h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  margin: 0 0 8px 0;
  position: relative;

  @media (max-width: $tablet-max-width) {
    padding-left: 6px;
  }

  @media (max-width: $mobile-max-width) {
    font-size: 16px;
    line-height: 140%;
    padding-right: 40px;
  }
}

.faq__item p {
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  margin: 0;
  padding-left: 6px;
  max-width: 532px;

  @media (max-width: $tablet-max-width) {
    max-width: 533px;
  }

  @media (max-width: $mobile-max-width) {
    font-size: 14px;
    line-height: 140%;
    padding-right: 39px;
  }
}
